#bar {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 0.25vh 2vw 0.75vh 2vw;
  max-width: 100vw;
}

.button:hover {
	-webkit-animation: flicker 0.3s 6;
	        animation: flicker 0.3s 6;
	-webkit-animation-timing-function: ease-in-out;
	        animation-timing-function: ease-in-out;
}

@-webkit-keyframes flicker {
  0%   { opacity:1; }
  50%  { opacity:0.5; }
  100% { opacity:1; }
}

@keyframes flicker {
  0%   { opacity:1; }
  50%  { opacity:0.5; }
  100% { opacity:1; }
}

#restart {
	display: inline-block;
  width: 3vw;
  min-width: 20px;
  max-width: 28px;
  height: 3vw;
  min-height: 20px;
  max-height: 28px;
}

#moves {
  display: inline-block;
  font-weight: bolder;
  font-size: 3vh;
  line-height: 0.8;
}

#options {
	display: inline-block;
  width: 3vw;
  min-width: 20px;
  max-width: 28px;
  height: 3vw;
  min-height: 20px;
  max-height: 28px;
}



